<style>
	.buttons {display:flex;flex-wrap:wrap;align-items:flex-end;padding:0 var(--wrapper-text) 0;margin-top:24px;margin-bottom:24px;}
	.buttons.dark {padding-top:36px;padding-bottom:24px;margin-top:0;margin-bottom:0;}
		.buttons > * {margin:0 24px 12px 0;}
		.buttons > *:last-child {margin-right:0;}
</style>

<h2 class="heading">Classes</h2>

<pre class="dark code"><code class="css">
{`<button [disabled] class="button
  [large|big|regular|medium|small|tiny]
  [red|green|blue]
  [outlined]
  [pending]
  [on-dark]">Button</button>`}
</code></pre>

<h2 class="heading">Colors</h2>

<section class="buttons">
	<button class="button">Default</button>
	<button class="red button">Red</button>
	<button class="green button">Green</button>
	<button class="blue button">Blue</button>

	<button class="outlined red button">Red</button>
	<button class="outlined green button">Green</button>
	<button class="outlined blue button">Blue</button>
</section>

<section class="buttons dark">
	<button class="on-dark button">Default</button>
	<button class="on-dark red button">Red</button>
	<button class="on-dark green button">Green</button>
	<button class="on-dark blue button">Blue</button>

	<button class="outlined on-dark red button">Red</button>
	<button class="outlined on-dark green button">Green</button>
	<button class="outlined on-dark blue button">Blue</button>
</section>

<h2 class="heading">Sizes</h2>

<section class="buttons">
	<button class="large button">Large</button>
	<button class="big button">Big</button>
	<button class="regular button">Regular</button>
	<button class="medium button">Medium</button>
	<button class="small button">Small</button>
	<button class="tiny button">Tiny</button>
</section>

<h2 class="heading">States</h2>

<section class="buttons">
	<button class="button">Default</button>
	<button class="current button">Current</button>
	<button disabled class="button">Disabled</button>
	<button disabled class="pending button">Pending</button>
</section>
